<template>
    <t-dialog theme="danger" header="警告" body="确定退出吗?" cancelBtn="取消" @confirm="logout()" v-show="local_update_visible"
        :onClose="onCloseDialogLogout">

    </t-dialog>
</template>

<script>

import bus from '@/utils/bus.js'
export default {
    name: 'Logout',

    props: {
        visible: Boolean
    },

    data() {
        return {
            local_update_visible: false,
        }
    },

    methods: {
        onCloseDialogLogout() {
            this.local_update_visible = false;
            this.$emit('onCloseDialogLogout', false);
        },

        logout() {
            // if (!sessionStorage.is_remember_me && !JSON.parse(sessionStorage.is_remember_me)) {
            //     sessionStorage.clear();
            // } else {
            //     sessionStorage.removeItem('user_id')
            //     sessionStorage.removeItem('token')
            // }

            bus.$emit('isDarkmode', sessionStorage.isDark)

            this.$router.push('/')
        },
    },

    watch: {
        visible(news, olds) {
            this.local_update_visible = news
        }
    },
}
</script>

