<template>
	<t-card bordered class="t_card">
		<template #actions>
			<t-switch size="large" v-model="isDark" style="margin-right:10px" :onChange="changeThemeMode"
				:label="['白天', '夜间']">
			</t-switch>
		</template>
		<t-space style="width:100%; height:100%;display:flex" theme-mode="dark">
			<t-space>
				<t-space direction="vertical" align="start" class="header_box">
					<t-image :src="require('@/assets/images/SVG/logo.png')" position="top" class="header_img" alt="LOGO"
						error="LOGO" />
					<div></div>
					<span class="header_title">{{ header_title }}</span>
				</t-space>
				<t-space direction="vertical" class="main_box">
					<t-tabs size="large" defaultValue="login" :value="value" @change="(newValue) => (value = newValue)">
						<!-- 登陆选项卡 -->
						<t-tab-panel value="login">
							<template #label>
								<icon name="login" style="margin-right: 4px" /> 登录
							</template>
							<Login />
						</t-tab-panel>
						<!-- 注册选项卡 -->
						<t-tab-panel value="register">
							<template #label>
								<icon name="user-add" style="margin-right: 4px" /> 注册
							</template>
							<Register />
						</t-tab-panel>
					</t-tabs>
				</t-space>
			</t-space>
			<!-- 轮播图 -->
			<SwiperImage />
		</t-space>
	</t-card>
</template>
  
<script>
import { Icon } from 'tdesign-icons-vue';
import Login from '@/components/user/Login.vue'
import Register from '@/components/user/Register.vue'
import SwiperImage from '@/components/user/SwiperImage.vue';
import bus from '@/utils/bus.js'

export default {
	components: {
		Login,
		Register,
		SwiperImage,
		Icon,
	},
	data() {
		return {
			value: 'first',
			isDark: true,
			header_title: '欢迎！',
		};
	},


	mounted() {

		bus.$on('isDarkMode', (data) => {
			this.isDark = (this.isDark == null ? false : data)
		})

		if (!this.isDark) {
			document.documentElement.setAttribute('theme-mode', 'dark')
		}
		else document.documentElement.removeAttribute('theme-mode');
	},

	methods: {
		// 切换暗色模式
		changeThemeMode() {
			// 设置暗色模式
			this.isDark = !this.isDark
			if (!this.isDark) {
				document.documentElement.setAttribute('theme-mode', 'dark')
			} else {
				document.documentElement.removeAttribute('theme-mode');
			}
		},

	},

	watch: {
		isDark(news, olds) {
			this.isDark = news
		},
	}
};
</script>

<style lang="less" scoped>
.t_card {
	width: 100%;
	height: 100%;

	.header_img {
		width: 500px;
		background: transparent;
	}

	.main_box {
		position: absolute;
		display: flex;
		align-content: center;
		align-items: center;
		top: 55%;
		left: 25%;
		transform: translate(-50%, -50%);
	}

	.header_title {
		font-size: 50px;
		font-weight: light;
		margin-top: 30px;
	}

	.header_box {
		position: absolute;
		display: flex;
		align-content: center;
		align-items: center;
		top: 20%;
		left: 25%;
		transform: translate(-50%, -50%);
	}
}
</style>
  